<template>
<div >
    <loading :active.sync="isLoading" >
    <div class="dot-loader">
        <span class="dot dot1 bg-use"></span>
    <span class="dot dot2 bg-abstract"></span>
    <span class="dot dot3 bg-warning"></span>
    <span class="dot dot4 bg-alert"></span></div>
    <i class="fas fa-fish text-primary me-2 fa-3x"></i>
    <div class="dot-loader">
        <span class="dot dot5 bg-use"></span>
    <span class="dot dot6 bg-abstract"></span>
    <span class="dot dot7 bg-warning"></span>
    <span class="dot dot8 bg-alert"></span></div>
</loading> 
  <div class="img-header bg-cover d-flex justify-content-center align-items-center"
    style="background-image: url('https://images.unsplash.com/photo-1599488615731-7e5c2823ff28?ixid=MnwxMjA3fDB8MHxzZWFyY2h8OTZ8fGZpc2glMjBhcXVhcml1bXxlbnwwfHwwfHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=500&q=60')">
        <h2 class="img-header-title"><strong></strong></h2>
    </div>
<div class="container-fluid py-5">
     <div class="row">
     <div class="col-md-6">
  <Breadcrumb></Breadcrumb>
  </div>
  </div>
    <div class="row justify-content-center align-items-center"> 
<div class="col-md-6">
<form class="pt-2 pb-5" @submit="signin">
    <div class="text-center pb-3">
        <div class="dot-group">
        <span class="dot dot1 bg-use"></span>
    <span class="dot dot2 bg-abstract"></span>
    <span class="dot dot3 bg-warning"></span>
    <span class="dot dot4 bg-alert"></span></div>
    <i class="fas fa-fish text-primary me-2 fa-3x"></i>
    <div class="dot-group">
        <span class="dot dot5 bg-use"></span>
    <span class="dot dot6 bg-abstract"></span>
    <span class="dot dot7 bg-warning"></span>
    <span class="dot dot8 bg-alert"></span></div>
    </div>
<!-- <h1 class="h3 mb-3 fw-bold text-center text-third"></h1> -->

<div class="form-floating mb-3">
    <input type="email" placeholder="" class="form-control bg-light text-third" id="floatingInput"  v-model="user.username">
    <label for="floatingInput"></label>
</div>
<div class="form-floating mb-3">
    <input type="password"  placeholder="" class="form-control bg-light text-third" id="floatingPassword"  v-model="user.password" autocomplete="on">
    <label for="floatingPassword"></label>
</div>


<button class="w-100 btn btn-lg btn-abstract fw-bold" type="submit"></button>

</form>
</div>
</div>
</div>  

</div>
</template>
<script>
import {mapGetters} from 'vuex'
import Breadcrumb from '../breadcrumb'

export default{
name:'login',
data(){
return{
    user:{
        username:'',
        password:''
    }
}
},components:{
    Breadcrumb
},

computed:{
... mapGetters(['isLoading','checkoutVisible']),

},
methods: {
signin(){
        let api = `${process.env.VUE_APP_APIHOST}/admin/signin`;
        const vm =this;
        this.$store.dispatch('updatedLoading',true);
        this.$http.post(api,vm.user).then((res)=>{
            if(res.data.success){
                const token =res.data.token;
                const expired =res.data.expired;
                document.cookie=`hexToken=${token};expires=${new Date(expired)};`;
                vm.$store.dispatch('updatedLoading',false);
                 vm.$store.dispatch('getLoginStatus',true);
                vm.$router.push('/admin/dashboard');
            }else{
                vm.$store.dispatch('updateMessage', {
                 message: res.data.message,
                status: 'danger',
                });
                 vm.$store.dispatch('getLoginStatus',false);
                vm.$store.dispatch('updatedLoading',false);        
            }
        }).catch((err)=>{
                console.log(err);
            })
}
},
created(){
     this.$store.dispatch('updateCheckoutBtn',true);
}
}
</script>