<template>
<div >
    <loading :active.sync="isLoading">
    <div class="dot-loader">
        <span class="dot dot1 bg-use"></span>
    <span class="dot dot2 bg-abstract"></span>
    <span class="dot dot3 bg-warning"></span>
    <span class="dot dot4 bg-alert"></span></div>
    <i class="fas fa-fish text-primary me-2 fa-3x"></i>
    <div class="dot-loader">
        <span class="dot dot5 bg-use"></span>
    <span class="dot dot6 bg-abstract"></span>
    <span class="dot dot7 bg-warning"></span>
    <span class="dot dot8 bg-alert"></span></div>
</loading> 
    <div class="img-header bg-cover d-flex justify-content-center align-items-center"
    style="background-image: url('https://images.unsplash.com/photo-1595541718888-f29d5deaeb6e?ixid=MnwxMjA3fDB8MHxzZWFyY2h8MjU1fHxmaXNoJTIwYXF1YXJpdW18ZW58MHx8MHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=500&q=60')">
        <h2 class="img-header-title"><strong>-</strong></h2>
    </div>

<div class="container-fluid py-5">
        <!------->
<div class="row">
    <div class="col-md-6">
<Breadcrumb></Breadcrumb>
</div>
</div>
    <div class="row justify-content-center ">
        <div class="col-md-5">
                <div class="content-striped">
            <div class="row mb-2 pb-2 align-items-center" v-for="item in cart" :key="item.id">
            <div class="col-6 col-sm-4 col-md-4 col-lg-3">
                <img :src="item.product.imageUrl" alt="item.product.title" class="cart-img-sm">
            </div>
            <div class="col-6 col-sm-8 col-md-8 col-lg-9">
                <div class="row align-items-center">
                    <div class="col-md-10 col-sm-10">
                        <small>{{item.product.title}} <span class="ms-1">{{item.qty}}</span></small>
                    <br><small>{{item.product.price | currency}}</small>
                    </div>                       
                    <div class="col-md-2 col-sm-2 col-3">
                        <button @click="deleteCartItem(item.id)" class="btn py-0 text-third">
                            <i class="fas fa-trash-alt"></i></button>
                    </div>
                </div>      
            </div>
        </div> 
        </div>
        <div class="row">
<div class="d-flex justify-content-end py-2" v-if="cartObjLength>0">
        <div class="me-4"></div>
        <div >{{cartObjTotal | currency}}</div>
    </div>
    <div  v-if="finalTotal!==cartObjTotal" class="d-flex justify-content-end py-2">
        <div class="text-success me-4 "></div>
        <div class="text-success ">{{finalTotal | currency}}</div>
    </div>
    </div>
    <div class="row">
    <div v-if="cart.length>0">
<button class="btn btn-outline-third mb-3" @click="isOpenCoupon=!isOpenCoupon" type="button" data-bs-toggle="collapse" data-bs-target="#collapseExample" aria-expanded="false" aria-controls="collapseExample">
 <i class="fas" :class="{'fa-chevron-up':isOpenCoupon,'fa-chevron-down':!isOpenCoupon}"></i>
</button>
<div class="collapse mb-3 " id="collapseExample">
<div class="card card-body bg-light">
        <div class="row justify-content-center align-items-center">
    <div class="col-12">
    <div class="content-striped">
    <div class="row mb-2 pb-2  align-items-center" 
        v-for="item in AllActiveCouponList" :key="item.id" v-cloak >
                <div class="col-12  d-flex">
                <div class="coupon text-white p-1">
                  <i class="fas fa-tags"></i>
                     <strong class="ps-2">{{item.percent}}%</strong>
                </div>
              <div class="ms-3">
                     <p class="mb-0">{{item.title}}</p>
                     <!--  <small>{{couponDeadline(item.due_date)}}</small>-->
             </div> 
                </div>
                <div class="col-12">
                    <div class="row align-items-center">
         <div class="input-group input-group-sm my-3">
       <button class="btn-third text-white btn" @click="HandleCopy"><i class="far fa-clone ms-1"></i></button>
            <input type="text" readonly :value="item.code" class="bg-light input-readonly" >
         </div>
        </div>               
        </div>
    </div>
    </div>
  <div v-if="!AllActiveCouponList.length&&AllActiveCouponList"  class="py-5 text-center">
        <h3>!</h3>
    </div> 
    </div>
    </div>
</div>
</div>
    </div>
<div class="input-group input-group-sm mb-3" v-if="cart.length>0">
<input type="text" class="form-control" v-model="coupon_code" placeholder="" aria-label="" aria-describedby="button-addon">
<div class="input-group-append">
<button class="btn btn-warning" type="button" id="button-addon" @click="useCoupon"></button>
</div>
</div>
</div>

<div class="row">
    <div v-if="cart.length===0" class="text-center py-5">
        <div class="py-3">
            <i class="fa-lg fas fa-shopping-cart fa-3x mb-3 text-third"></i>
        </div>
<router-link class="btn btn-primary text-third" to="/product">
<i class="fas fa-angle-double-right ms-2"></i></router-link>   
</div>
</div>
</div>
        <div class="col-md-7">
<div class="row justify-content-center align-items-center">
<div class="col-md-11">
    <ValidationObserver v-slot="{ invalid }">
<form  @submit.prevent="createOrder" >
<div class="row">
    <div class="col-md-6  mb-4">
    <ValidationProvider rules="required" v-slot="{ errors, classes }">           
        <label for="userName"><span class="text-danger">*</span></label>
        <input type="text" name="" id="userName" class="form-control " v-model="user.name" :class="classes"> 
        <span class="invalid-feedback">{{ errors[0] }}</span>
        </ValidationProvider> 
    </div>
    <div class="col-md-6  mb-4">
        <ValidationProvider rules="required|email" v-slot="{ errors, classes }">
            <label for="email"><span class="text-danger">*</span></label>
        <input type="email" name="" id="email" v-model="user.email" class="form-control" :class="classes">
        <span class="invalid-feedback" >{{ errors[0] }}</span>
        </ValidationProvider> 
    </div>
</div>
    <div class="row">
    <div class="col-md-6  mb-4">
        <ValidationProvider rules="required|digits:10" v-slot="{ errors, classes }">
        <label for="phone"><span class="text-danger">*</span></label>
        <input type="tel" name="" id="phone" v-model="user.tel" class="form-control" :class="classes">
        <span class="invalid-feedback">{{ errors[0] }}</span>
        </ValidationProvider> 
        </div>

    <div class="col-md-6 mb-4">
        <ValidationProvider rules="required" v-slot="{ errors, classes }">
            <label for="email"><span class="text-danger">*</span></label>
    <select class="form-select " :class="classes" name="">
        <option value="payafter" selected></option>
        <option value="creditcard"></option>
    </select>
        <span class="invalid-feedback">{{ errors[0] }}</span>
        </ValidationProvider> 
    </div>
</div>
    <div class="row">
    <div class="col-md-12  mb-4">
    <ValidationProvider rules="required" v-slot="{ errors, classes }">
        <label for="address"><span class="text-danger">*</span></label>
        <input type="text" name="" id="address" v-model="user.address" class="form-control" :class="classes">
        <span class="invalid-feedback" >{{ errors[0] }}</span>
        </ValidationProvider> 
    </div>
</div>
<div class="row">
    <div class="col-md-12 mb-4">
        <label for="message"></label>
        <textarea name="" id="message" cols="30" rows="3" v-model="user.message" class="form-control"></textarea>             
    </div>
</div>
    <div class="text-end mb-4">
    <button type="submit" class="btn btn-abstract" :disabled="invalid||cart.length===0" ><i class="fas fa-angle-double-right ms-2"></i></button>
    </div>
</form>
</ValidationObserver>
</div>
</div>
</div>
</div>
</div>
</div>
</template>
<script>
import {mapGetters} from 'vuex'
import Breadcrumb from '../breadcrumb'
import $ from 'jquery'
export default{
name:'Checkout1',
components:{
Breadcrumb
},
data(){
return{
coupon_code:'',
user:{
name: "",
email: "",
tel: "",
address: "",
message:''
},
isOpenCoupon:false,
AllActiveCouponList:[],
}
},
created(){
this.$store.dispatch('getCart');
this.$store.dispatch('updateCheckoutBtn',false);
this.getAllCoupons();
},
computed:{
...mapGetters(['cart','isLoading','cartObjLength','cartObjTotal','finalTotal','checkoutVisible']),
},
methods: {

deleteCartItem(id){
this.$store.dispatch('deleteCartItem',id);
},
useCoupon(){
this.$store.dispatch('useCoupon', this.coupon_code);
},
createOrder(){
const api=`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/order`;
const vm=this;
this.$store.dispatch('updatedLoading',true);
//
let textMessage = vm.user.message.trim().replace(/[&<>"'`=\/]/g,"");
this.$http.post(api,{data:{ user:vm.user},message:textMessage}).then((res)=>{
    if(res.data.success){
    
        vm.$router.push(`/checkoutPay/${res.data.orderId}`);
    }else{
        vm.$store.dispatch('updateMessage', {
            message: res.data.message,
            status: 'danger',
        });
    }
        //update CART
    vm.$store.dispatch('getCart'); 
    this.$store.dispatch('updatedLoading',false);
}).catch((err)=>{
    console.log(err);
});
},
HandleCopy(e){
 $(e.target).parent().find('input[type="text"]').select();
  document.execCommand("copy");
},
 getAllCoupons(){

      this.AllActiveCouponList=[{id:'1',percent:80,code:'newfish123',title:'!'},
      {id:'2',percent:85,code:'free123',title:''},
      {id:'3',percent:95,code:'weed123321',title:''}]
}
},

}
</script>