<template>
    <div>
        <div class="row ">
           <div class="col-md-4 col-sm-6 mb-4" v-for="item in product" :key="item.id">
 <router-link :to="`/content/${item.id}`">
    <div class="card bg-light h-100 card-shadow" >
        <div class="card-img-wrapper">
            <div class="view-more d-flex justify-content-center align-items-center"></div>
              <Myfavorite :productID="item.id"></Myfavorite>
<img :src="item.imageUrl" class="card-img card-img-size" :alt="item.title"></div>
<div class="card-body pb-1">
      <p class="card-title  text-third mb-0"><strong>{{ item.title }}</strong></p> 
      <p class="text-third mb-1 card-description"><small>{{item.description}}</small></p>
      <div class="d-flex justify-content-between">
      <span class="text-decoration-line-through text-third "><small>{{item.origin_price |currency }}</small></span>
      <strong  class="text-alert">NT {{item.price | currency}} </strong>
      </div>
</div>
<div class="card-footer border-0 p-0 bg-light"> 
    <button class="btn rounded-0 btn-block btn-warning w-100 card-btn" @click.prevent="addToCart(item.id)">
        <i class="ms-2 fas fa-cart-plus "></i>
           <i class="fas fa-spinner fa-spin ms-2" v-if="loadingItem===item.id"></i>
        </button></div>
</div></router-link>
    </div>
    </div>
     </div>
</template>
<script>
import Myfavorite from './myFavorite.vue'
export default{
name:'productCard',
props:['product'],
components:{
Myfavorite
},
methods: {
  addToCart(id,qty=1){
   this.$store.dispatch('AddtoCart', { id,qty});
  }
},
computed:{
   loadingItem(){
   return this.$store.state.status.loadingItem;
}
}
}
</script>