<template>
    <div class="pt-5">
            <loading :active.sync="isLoading" >
                <div class="dot-loader">
        <span class="dot dot1 bg-use"></span>
    <span class="dot dot2 bg-abstract"></span>
    <span class="dot dot3 bg-warning"></span>
    <span class="dot dot4 bg-alert"></span></div>
    <i class="fas fa-fish text-primary me-2 fa-3x"></i>
    <div class="dot-loader">
        <span class="dot dot5 bg-use"></span>
    <span class="dot dot6 bg-abstract"></span>
    <span class="dot dot7 bg-warning"></span>
    <span class="dot dot8 bg-alert"></span></div>
            </loading> 
  <h2 class="mt-4 manage-header"><i class="fas fa-calendar ms-2"></i></h2>
     <div class="table-responsive">
          <table class="table table-striped table-sm mt-4 text-third" v-if="todayOrders">
            <thead>
                <th width="60"></th>
                <th width="120"></th>
                  <th width="80"></th>
                <th width="60"></th>
                <th width="60"></th>
            </thead>
            <tbody>
                <tr v-for="(item) in todayOrders" :key="item.id">
                    <td>{{TransformDate(item.create_at)}}</td>
                    <td>
                        <ul class="list-unstyled mb-0">
                        <li v-for="product in item.products" :key="product.id">
                            {{product.product.title}}  {{product.qty}}
                        </li>
                        </ul>
                    </td>    
                    <td >{{item.total | currency}}</td>
                    <td ><span class="text-success" v-if="item.is_paid"></span>
                         <span class="text-danger" v-else></span></td>
                    <td>
                        <button class="btn btn-outline-third btn-sm"
                        @click="OpenModal(item)"></button>   
                    </td>                    
                </tr>
            </tbody>
        </table>
   
          <div v-else-if="todayOrders.length==0">
               <h3 class="text-center"></h3>
          </div>
          <!--TodayOrderModal--->
          <div class="modal fade"  id="TodayOrderModal" tabindex="-1"  aria-hidden="true">
<div class="modal-dialog modal-dialog-scrollable modal-xl">
<div class="modal-content">
<div class="modal-header bg-abstract bg-gradient text-white">
<h5 class="modal-title"><i class="fas fa-check-square ms-2"></i></h5>
<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
</div>
<div class="modal-body">
<div class="row">
    <div class="col-md-6 py-2 mb-2">
    <label for="datetime" class="manage-header"><strong></strong></label>
    <p class="manage-text mb-0"> {{TransformDate(currentOrder.create_at)}}</p>
   </div>
<div class="col-md-6 py-2">
    <label for="orderNo" class="manage-header"><strong></strong></label>
    <p class="manage-text mb-0">{{currentOrder.id}}</p>
</div>
</div>
<div class="row  bg-light">
<div class="col-md-6 py-2">
    <label for="total" class="manage-header"><strong></strong></label>
      <p class="manage-text mb-0"> {{currentOrder.total | currency}}</p>
  </div>
<div class="col-md-6 py-2">
    <label for="isPaid" class="manage-header"><strong></strong></label>
      <p class="manage-text mb-0"> {{currentOrder.is_paid?'':''}}</p>
  </div>
</div>
<div class="row">
<div class="col-md-12 py-2">
        <label class="manage-header "><strong></strong></label>
        <ul class="manage-text mb-0 list-point-inset"> 
            <li v-for="product in currentOrder.products" :key="product.id">{{product.product.title}} 
                 {{product.qty}}</li>
        </ul>
</div>
</div>
<div class="row  bg-light">
<div class="col-md-6 py-2">
    <label for="name" class="manage-header"><strong></strong></label>
     <p class="manage-text mb-0"> {{currentOrder.user.name}}</p><!--     <input type="text" class="form-control" id="name"  :value="currentOrder.user.name"> -->
</div>
<div class="col-md-6 py-2">
    <label for="email" class="manage-header"><strong>email</strong></label>
    <p class="manage-text mb-0"> {{currentOrder.user.email}}</p><!--     <input type="email" class="form-control" id="email"  :value="currentOrder.user.email"> -->
</div>
</div>
<div class="row">
<div class="col-md-6 py-2">
    <label for="tel" class="manage-header"><strong></strong></label>
     <p class="manage-text mb-0"> {{currentOrder.user.tel}}</p>
</div>
<div class="col-md-6 py-2">
    <label for="address" class="manage-header"><strong></strong></label>
     <p class="manage-text mb-0"> {{currentOrder.user.address}}</p>
</div>
</div>
<div class="row  bg-light">
    <div class="col-md-12 py-2">
        <label class="manage-header"><strong></strong></label>
    <p class="manage-text mb-0"> {{currentOrder.user.message}}</p><!--     <textarea name="" id="" cols="30" rows="3" class="form-control" :value="currentOrder.user.message"></textarea> -->
    </div>
</div>
</div>
</div>
</div>
</div>
</div>
    </div>
</template>

<script>
import {mapGetters} from 'vuex' 

export default ({
name:'reportDay',
data(){
    return{
        todayOrders:[],
        currentOrder:{
            user:{}
        },
        ViewModal:null
    }
},
computed:{
...mapGetters(['isLoading']),
},
methods: {
  getTodayOrders(){
       let page = 0;
      let totalPage = 1;
      let items = [];
      let counter=1;
      const api = `${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/admin/orders?page=${page}`;
      const vm = this;
      this.$store.dispatch("updatedLoading", true);
      this.$http
        .get(api)
        .then((res) => {
          if (res.data.success) {
            items = res.data.orders;
            totalPage = res.data.pagination.total_pages;
           
             for (let i = 2; i < totalPage + 1; i++) {
              let apiurl = `${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/admin/orders?page=${i}`;
              vm.$http
                .get(apiurl)
                .then((res) => {
                  if (res.data.success) { 
                      items=[...items,...res.data.orders];
                      counter=counter+1;
                    
                  } else {
                    vm.$store.dispatch("updateMessage", {
                      message: res.data.message,
                      status: "danger",
                    });
                  } 
                  vm.$store.dispatch("updatedLoading", false);
                }).then(()=>{
                if(counter===totalPage){                
                 const today = new Date();
                  let todayStr = `${today.getFullYear()}/${today.getMonth() +1}/${today.getDate()}`;

                  vm.todayOrders=items.filter(item=>{  
                  const date = new Date(item.create_at * 1000);
                  let dateStr = `${date.getFullYear()}/${date.getMonth() +1}/${date.getDate()}`; 
                  return todayStr === dateStr;
            });
                      
          }
        })
                .catch((err) => {
                  console.log(err);
                });
                
            } 
          } else {
            vm.$store.dispatch("updateMessage", {
              message: res.data.message,
              status: "danger",
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
  },
  TransformDate(timestamp){
      let date= new Date(timestamp*1000);
      return `${date.getFullYear()}/${date.getMonth()+1}/
      ${date.getDate()} ${date.getHours()}:${date.getMinutes()}`;
  },
  OpenModal(item){
      this.currentOrder=Object.assign({},item);
      this.ViewModal.show();
  } 
},
created() {
    this.getTodayOrders();

},
 mounted(){
    this.ViewModal=new bootstrap.Modal(document.getElementById('TodayOrderModal'));
       
    }
})
</script>
