<template>
<div >
    <loading :active.sync="isLoading" >
    <div class="dot-loader">
        <span class="dot dot1 bg-use"></span>
    <span class="dot dot2 bg-abstract"></span>
    <span class="dot dot3 bg-warning"></span>
    <span class="dot dot4 bg-alert"></span></div>
    <i class="fas fa-fish text-primary me-2 fa-3x"></i>
    <div class="dot-loader">
        <span class="dot dot5 bg-use"></span>
    <span class="dot dot6 bg-abstract"></span>
    <span class="dot dot7 bg-warning"></span>
    <span class="dot dot8 bg-alert"></span></div>
</loading> 
<div class="img-header bg-cover d-flex justify-content-center align-items-center"
style="background-image: url('https://images.unsplash.com/photo-1529057018378-07c51d77da73?ixid=MnwxMjA3fDB8MHxzZWFyY2h8MjY3fHxmaXNoJTIwdGFua3xlbnwwfHwwfHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=500&q=60')">
    <h2 class="img-header-title"><strong>-</strong></h2>
</div>
    <div class="container py-5">
    <div class="row">
<div class="col-md-6">
<Breadcrumb></Breadcrumb>
</div>
</div>
            <div class="row justify-content-center align-items-center">
        <form class="col-md-12"  @submit.prevent="payOrder" >
        <h3  class="text-third"><i class="fas fa-shopping-bag me-2"></i></h3>
   
    <div class="accordion accordion-flush mb-3" id="orderDetail">
  <div class="accordion-item">
    <h2 class="accordion-header" id="flush-headingOne">
      <button class="accordion-button collapsed " type="button" data-bs-toggle="collapse" data-bs-target="#orderDetailContent" aria-expanded="false" aria-controls="flush-collapseOne">
       <span class="h5 ">:<span class="ms-2"  v-cloak v-if="order.total">
                        {{order.total | currency}}</span></span>
      </button>
    </h2>
    <div id="orderDetailContent" class="accordion-collapse collapse" aria-labelledby="flush-headingOne" data-bs-parent="#orderDetail">
      <div class="accordion-body">
       <div class="content-striped">
            <div class="row mb-2 pb-2 align-items-center" v-for="item in order.products" :key="item.id">
            <div class="col-6 col-sm-4 col-md-4 col-lg-3">
                <img :src="item.product.imageUrl" alt="item.product.title" class="cart-img-sm">
            </div>
            <div class="col-6 col-sm-8 col-md-8 col-lg-9">
                <div class="row align-items-center">
                    <div class="col-md-6">
                        <small>{{item.product.title}} <span class="ms-1">{{item.qty}}</span></small>
                      <span class="badge bg-success ms-2" v-if="item.total!==item.final_total">
                    
                </span>
                    </div>                       
                    <div class="col-md-3">
                     {{item.product.price | currency}}
                   </div>
                    <div class="col-md-3 text-success" v-if="item.total!=item.final_total">
                   {{item.final_total | currency}}</div>
                    </div>
                     
            </div>
        </div> 
        </div>
      </div>
    </div>
  </div>
</div>

        <h3 class="text-third"><i class="fas fa-user me-2"></i></h3>
    <div class="table-responsive">
            <table class="table table-striped bg-light">             
            <tbody >
                <tr>
                    <th   class="ps-5">Email</th>
                    <td  v-cloak class="ps-5">{{ order.user.email }}</td>
                </tr>
                <tr>
                <th  class="ps-5"></th>
                <td  v-cloak class="ps-5">{{ order.user.name }}</td>
                </tr>
            <tr>
                <th  class="ps-5"></th>
                <td  v-cloak class="ps-5">{{ order.user.tel }}</td>
            </tr>
                <tr>
                    <th  class="ps-5"></th>
                    <td  v-cloak class="ps-5">{{ order.user.address }}</td>
            </tr>
            <tr>
                <th  class="ps-5"></th>
            <td  class="ps-5">
                <span v-if="!order.is_paid"></span>
                <span v-else class="text-success"></span>
            </td>
        </tr>
    </tbody>
</table>
    </div>
    <div class="text-end mb-4" >
        <button type="submit" class="ms-2 btn btn-abstract" ><i class="fas fa-angle-double-right ms-2"></i></button>
     </div>
        </form>
        </div>
    </div>
  
</div>
</template>
<script>
import {mapGetters} from 'vuex'
import Breadcrumb from '../breadcrumb'

export default{
name:'Checkout2',
components:{
Breadcrumb
},
computed:{
...mapGetters(['isLoading']),
},
data(){
return{
    orderId:'',
    order:{
        user:{

        }
    },
    isDiscount:false
}
},
methods:{
getOrder(){
    const api=`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/order/${this.orderId}`;
        const vm=this;
        this.$store.dispatch('updatedLoading',true);
        this.$http.get(api).then((res)=>{
            if(res.data.success){
                
                vm.order=Object.assign({},res.data.order);
            }else{
                vm.$store.dispatch('updateMessage', {
                    message: res.data.message,
                    status: 'danger',
                });
                
            }
            this.$store.dispatch('updatedLoading',false);
        }).catch((err)=>{
            console.log(err);
        });
},
    payOrder(){
        const api=`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/pay/${this.orderId}`;
        const vm=this;
        this.$store.dispatch('updatedLoading',true);
        this.$http.post(api).then((res)=>{
            if(res.data.success){
              this.$router.push('/checkoutSuccess');
            }else{
                vm.$store.dispatch('updateMessage', {
                    message: res.data.message,
                    status: 'danger',
                });                  
            }
                    
            vm.$store.dispatch('updatedLoading',false);
        
        }).catch((err)=>{
            console.log(err);
        });
    }
},
created(){
this.orderId= this.$route.params.orderId;
this.getOrder();
}
}
</script>