<template>
    <div class="pt-5">
        <loading :active.sync="isLoading" >
                <div class="dot-loader">
        <span class="dot dot1 bg-use"></span>
    <span class="dot dot2 bg-abstract"></span>
    <span class="dot dot3 bg-warning"></span>
    <span class="dot dot4 bg-alert"></span></div>
    <i class="fas fa-fish text-primary me-2 fa-3x"></i>
    <div class="dot-loader">
        <span class="dot dot5 bg-use"></span>
    <span class="dot dot6 bg-abstract"></span>
    <span class="dot dot7 bg-warning"></span>
    <span class="dot dot8 bg-alert"></span></div>
            </loading> 
    <h2 class="mt-4 manage-header"><i class="fas fa-truck ms-2"></i></h2>
        <div class="table-responsive">
          <table class="table table-striped table-sm mt-4 text-third" v-if="Orders">
            <thead>
               <th width="120"></th>
               <th  width="140">Email</th>
               <th class="140"></th>
               <th class="text-end pe-3" width="100"></th>
               <th  width="100"></th>
               <th   width="100"></th>
            </thead>
            <tbody>
                <tr v-for="item in Orders" :key="item.id">
                   <td>{{getCreatedDate(item)}}</td>
                    <td>{{item.user.email}}</td>
                    <td>
                    <ul class="ps-0">
                    <li v-for="product in item.products" :key="product.id" class="list-insie">
                        {{product.product.title}} {{product.qty}}{{product.product.unit}}
                    </li>
                    </ul>
                    </td>
                    <td class="text-end pe-3">{{item.total | currency}}</td>
                    <td><span class="text-success" v-if="item.is_paid"></span><span v-else class="text-danger"></span></td>
                    <td><button class="btn btn-outline-third" @click="OpenModal(item)"></button></td>
                </tr>
            </tbody>
        </table>
          <div v-else-if="Orders.length==0">
               <h3 class="text-center"></h3>
         </div>
    </div>
        <pagination :pagination="OrderPagination" @getData="getOrders"></pagination>  
                    <!-- Modal -->
<div class="modal fade" id="ViewModal" tabindex="-1" aria-labelledby="ViewModalabel" aria-hidden="true">
<div class="modal-dialog modal-xl modal-dialog-scrollable" role="document">
<div class="modal-content border-0">
    <div class="modal-header bg-abstract text-white">
    <h5 class="modal-title" id="ViewModalLabel">
        <i class="fas fa-check-square ms-2"></i>
    </h5>
     <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
    </div>
    <div class="modal-body">
    <div class="row">
<div class="col-md-6 py-2 mb-2">
    <label for="datetime" class="manage-header"><strong></strong></label>
    <p class="manage-text mb-0"> {{getCreatedDate(currentOrder)}}</p>
</div>
<div class="col-md-6 py-2">
    <label for="orderNo" class="manage-header"><strong></strong></label>
    <p class="manage-text mb-0">{{currentOrder.id}}</p>
</div>
</div>
<div class="row">
<div class="col-md-6 py-2 bg-light">
    <label for="total" class="manage-header"><strong></strong></label>
      <p class="manage-text mb-0"> {{currentOrder.total | currency}}</p>
</div>
<div class="col-md-6 py-2 bg-light">
    <label for="isPaid" class="manage-header"><strong></strong></label>
      <p class="manage-text mb-0"> {{currentOrder.is_paid?'':''}}</p>
   </div>
</div>
<div class="row">
<div class="col-md-12 py-2">
        <label class="manage-header "><strong></strong></label>
        <ul class="manage-text mb-0 list-point-inset"> 
            <li v-for="product in currentOrder.products" :key="product.id">{{product.product.title}} 
                {{product.qty}}{{product.product.unit}}</li>
        </ul>
</div>
</div>
<div class="row">
<div class="col-md-6 py-2 bg-light">
    <label for="name" class="manage-header"><strong></strong></label>
     <p class="manage-text mb-0"> {{currentOrder.user.name}}</p>
</div>
<div class="col-md-6 py-2 bg-light">
    <label for="email" class="manage-header"><strong>email</strong></label>
    <p class="manage-text mb-0"> {{currentOrder.user.email}}</p>
</div>
</div>
<div class="row">
<div class="col-md-6 py-2">
    <label for="tel" class="manage-header"><strong></strong></label>
     <p class="manage-text mb-0"> {{currentOrder.user.tel}}</p>
</div>
<div class="col-md-6 py-2">
    <label for="address" class="manage-header"><strong></strong></label>
     <p class="manage-text mb-0"> {{currentOrder.user.address}}</p>
</div>
</div>
<div class="row">
    <div class="col-md-12 py-2  bg-light">
        <label class="manage-header"><strong></strong></label>
    <p class="manage-text mb-0"> {{currentOrder.user.message}}</p>
</div>
</div>
    </div>
    </div>
</div>
</div>
    </div>
</template>
<script>
import pagination from '../paginationManage'
import {mapGetters} from 'vuex'
export default{
    name : 'order',
    components:{
        pagination
    },
    data(){
        return{
        currentOrder:{
            user:{}
        },
        viewModal:null
        }
    },
    methods:{
        getOrders(page=1){
          this.$store.dispatch('getOrder', page);
        },
        getCreatedDate(item){
         let date = new Date(item.create_at*1000);
         return `${date.getFullYear()}/${date.getMonth()+1}/${date.getDate()} ${date.getHours()}:${date.getMinutes()}`;
        },
        OpenModal(item){
            this.currentOrder=Object.assign({},item);
            this.viewModal.show();
        }

    },
    created(){
        this.getOrders();
        this.$store.dispatch('updateCheckoutBtn',true);
    },
    mounted(){
    this.viewModal= new bootstrap.Modal(document.getElementById('ViewModal'));
    },
    computed:{
      ...mapGetters(['isLoading','Orders','OrderPagination','checkoutVisible'])
    }
}
</script>