<template>
    <div class=" d-flex align-items-center order-md-1" v-if="checkoutVisible===true">
         <a class="cart nav-link" @click.prevent="cartVisible=!cartVisible">
      <i class="fa-lg fas fa-shopping-cart fa-lg"></i>
      <span class="cart-num badge rounded-pill bg-alert text-white"><small>{{cartObjLength}}</small></span>
      </a>
      <transition name="slide">
    <div class="cart-content bg-light rounded  py-2 px-2" v-if="cartVisible===true">
        <form >
          <div class="table-scroll">
    <table class="table table-sm" v-if="cartObjLength>0">
  <thead>
    <tr>
    <th width="10"></th>
    <th width="110"></th>
    <th width="40" class="text-end"></th>
    <th width="40" class="text-end"></th>
  </tr>
  </thead>
  <tbody>
    <tr v-for="item in cart" :key="item.id">
    <td valign="middle">
      <button @click="deleteCartItem(item.id)" class="btn p-0 text-third" v-if="checkoutVisible===true">
        <i class="fas fa-trash-alt fa-xs"></i>
        </button>
        </td>
    <td valign="middle">
      {{item.product.title}}     
<span class="badge bg-third rounded-pill" v-if="cartObjTotal!=finalTotal"></span>
    </td>
    <td  valign="middle" class="text-end">{{item.qty}}</td>
    <td  valign="middle" class="text-end">{{item.final_total | currency}}</td>
    </tr>
  </tbody>
</table>
</div>
        <div class="d-flex justify-content-end py-2 " v-if="cartObjLength>0"><!-- border-top -->
        <div class="me-4"></div>
          <div>{{finalTotal |currency}}</div>
    </div>
      <button type="button" @click="toCheckout" class="btn btn-abstract btn-block w-100 mb-2"  
      v-if="cartObjLength&&checkoutVisible===true">
<i class="fas fa-shopping-bag ms-2"></i>
 </button>
  
    <div class="px-2 py-2 text-center"  v-if="cartObjLength===0" >
      <i class="fa-lg fas fa-shopping-cart fa-3x mb-3"></i><br>
     <p><strong class="mb-3"></strong></p> 
      <router-link to="/product" class="btn btn-warning"><i class="fas fa-angle-double-right"></i></router-link>
    </div>
  </form>
    </div>      
      </transition>
      </div>
</template>
<script>
import {mapGetters} from 'vuex'
export default{
    name:'shoppingCart',
data(){
    return{
          cartVisible:false,    
          coupon_code:'',
    }
},
computed:{
  ...mapGetters(['cart','cartObjLength','cartObjTotal','finalTotal','checkoutVisible']),
},
created(){
     this.$store.dispatch('getCart');

},
methods: {
    deleteCartItem(id){
        this.$store.dispatch('deleteCartItem',id);
    },
    toCheckout(){
        this.cartVisible=false;
    
        this.$router.push('/checkoutCoupon');
    }
},
}
</script>