    <template>
        <div>
                <!--loader-->
    <loading :active.sync="isLoading" >
        <div class="dot-loader">
            <span class="dot dot1 bg-use"></span>
        <span class="dot dot2 bg-abstract"></span>
        <span class="dot dot3 bg-warning"></span>
        <span class="dot dot4 bg-alert"></span></div>
        <i class="fas fa-fish text-primary me-2 fa-3x"></i>
        <div class="dot-loader">
            <span class="dot dot5 bg-use"></span>
        <span class="dot dot6 bg-abstract"></span>
        <span class="dot dot7 bg-warning"></span>
        <span class="dot dot8 bg-alert"></span></div>
    </loading> 
    <!---->
        <div class="img-header bg-cover d-flex justify-content-center align-items-center"
        style="background-image: url('https://images.unsplash.com/photo-1516970739312-08b075784b71?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=755&q=80')">
            <h2 class="img-header-title"><strong></strong></h2>
        </div>
    <div class="container-fluid">
        <!------->
    <div class="row py-3">
        <div class="col-md-6">
    <Breadcrumb></Breadcrumb>
    </div>
    </div>
    <div class="row justify-content-center align-items-center">
    <div class="col-md-10">
    <div class="content-striped">
    <div class="row mb-2 pb-2  align-items-center" 
        v-for="item in products" :key="item.id" v-cloak >
                <div class="col-6 col-sm-4 col-md-3 col-lg-3">
                    <img :src="item.imageUrl" alt="item.title" class="cart-img">
                </div>
                <div class="col-6 col-sm-8 col-md-9 col-lg-9">
                    <div class="row align-items-center">
                    <div class="col-md-4">
                        <p class="badge bg-third"> {{item.category}}</p>
                <p>{{item.title}}</p>
            <div class="d-flex ">
                <p><del>{{item.origin_price | currency}}</del></p>
                <p class="text-alert h5 ms-5">{{item.price | currency}}</p>
            </div>
                    </div>
                    <div class="col-md-8">
                        <button class="btn btn-warning me-3"  role="button" @click="goToContent(item.id)"><span class="d-none d-sm-inline-block me-2"></span><i class="fas fa-angle-double-right"></i></button> 
    <button class="btn btn-abstract me-3" :id="item.id" role="button" @click="addToCart(item.id)"><span  class="d-none d-sm-inline-block me-2"></span><i class="fas fa-cart-plus"></i></button>
                <a class="" :id="item.id" role="button" @click="cancelFavorite(item.id)"><i class="fas fa-times ms-2"></i></a>

                    </div>
                    </div>
                    
                </div>
            </div>
    </div>
    <div v-if="!products.length" class="py-5 text-center">
        <h3>!</h3>
    </div>
    </div>
    </div>
    </div>
        </div>
    </template>
    <script>
    import Breadcrumb from '../breadcrumb.vue'
    import {mapGetters} from 'vuex'

    export default({
        name:'favorite',
        data(){
            return{
            products:[],

            }
        },
        components:{
        Breadcrumb 
        },
        computed:{
            ...mapGetters(['isLoading','favoritelist','checkoutVisible']),
        },
        methods: {
        getFavoriteList(){
            this.$store.dispatch('getFavorite');
            this.getProductsList();
        },
        getProductsList(){
        const api=`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/products/all`;
        const vm =this;
            this.products=[];
        this.$store.dispatch('updatedLoading',true);
        this.$http.get(api).then((res)=>{
            if(res.data.success){
                let productsAll= res.data.products;
                this.favoritelist.forEach(item => {
                    let match=  productsAll.filter((product)=>{
                    return product.id===item;
                }) 
                    if(match){
                        this.products.push(...match);
                    }
                });
            
            }
            else{
                vm.$store.dispatch('updateMessage', {
                message: res.data.message,
                status: 'danger',
            });
            }
                this.$store.dispatch('updatedLoading',false);
        }).catch((err)=>{
            console.log(err);
        });
        },
        goToContent(id){
            this.$router.push(`/content/${id}`);
        },
        cancelFavorite(id){
        
        this.$store.dispatch('removeFavorite',id);
        this.getFavoriteList();
        },
        addToCart(id){
            
            this.$store.dispatch('AddtoCart',{id});
        },
        },
        created() {
            this.getFavoriteList();
            this.$store.dispatch('updateCheckoutBtn',true);
        },
    })
    </script>
